
html,
body {
  width:100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Philosopher', sans-serif;
  background: -webkit-gradient(linear, left bottom, left top, from(#152336), to(#25435e));
  background: -webkit-linear-gradient(bottom, #152336, #25435e);
  background: -o-linear-gradient(bottom, #152336, #25435e);
  background: linear-gradient(to top, #152336, #25435e);
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  height: 100vh;
}

#map {
  bottom: 0;
  left: 15%;
  position: absolute;
  right: 0;
  height: 100%;
  z-index: 0;
}

.sidebar {
  width: 15%;
  height: 100%;
}

.app-name {
  padding-top: 10px;
  padding-bottom: 10px;
  color: #c1c1c1;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  margin: 0;
  font-size: 24px;
  margin-top:30px;
}

.search-container {
  display: -ms-flexbox;
  display: flex;
  margin-top: 50px;
  -ms-flex-direction: column;
      flex-direction: column;
}

.items {
  margin-top: 50px;
  width: 100%;
  color: #c1c1c1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.items h3 {
  font-size: 23px;
}

.each-item-container {
  opacity: 0.8;
  margin-top: 10px;
  margin-bottom: 10px;
  outline:none;
}

.each-item-container:focus {
    color:white;
}

.each-item-container h3 {
  margin-right: 15px;
  margin-left: 15px;
}

#hr {
  border: 0;
  height: 1px;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(rgba(255, 255, 255, 0.75)), to(rgba(0, 0, 0, 0)));
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
  background: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
  background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
  margin: 0;
  opacity: 0.3;
  padding-top: 1.4px;
}

.each-item-container:hover {
  opacity: 1 !important;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(255, 255, 255, 0.15)), color-stop(rgba(255, 255, 255, 0.05)),to(rgba(0, 0, 0, 0)));
  background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05),rgba(0, 0, 0, 0));
  background: -o-linear-gradient(bottom, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05),rgba(0, 0, 0, 0));
  background: linear-gradient(to top, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05),rgba(0, 0, 0, 0));
  cursor: pointer;
  color: #f1f1f1;
}

.input-wrapper input {
  width: 75%;
  display: -ms-flexbox;
  display: flex;
  margin: auto;
  padding: 8px 8px;
  font-size: 1em;
  border: 2px solid rgba(193, 193, 193, 0.50);
  border-radius: 15px;
  outline: none;
  -ms-flex-pack: center;
      justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  color: white;
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  text-align: center;
}

.input-wrapper input::-webkit-input-placeholder {
  color: rgba(193, 193, 193, 0.90);
  text-align: center;
}

.input-wrapper input:-ms-input-placeholder {
  color: rgba(193, 193, 193, 0.90);
  text-align: center;
}

.input-wrapper input::-ms-input-placeholder {
  color: rgba(193, 193, 193, 0.90);
  text-align: center;
}

.input-wrapper input::placeholder {
  color: rgba(193, 193, 193, 0.90);
  text-align: center;
}

.input-wrapper input:focus {
  border-radius: 5px;
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  border-color: #c1c1c1;
}

.input-wrapper input:focus::-webkit-input-placeholder { color:transparent; }
.input-wrapper input:focus:-moz-placeholder { color:transparent; }
.input-wrapper input:focus::-moz-placeholder { color:transparent; }
.input-wrapper input:focus:-ms-input-placeholder { color:transparent; }

.info-content {
  visibility: hidden;
  text-align: center;
  margin-top: 80px;
}

.marker-info {
  position: absolute;
  background-color: white;
  height: 100%;
  width: 400px;
  overflow: hidden;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  bottom: 0;
}

.wiki-image {
  max-width: 350px;
  height: 280px;
  margin: 25px;
}

.wiki-text {
  margin: 25px;
}

.open-close-button {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  position: absolute;
  right: 7px;
  top: 7px;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.arrow img {
    width: 40px;
    height: 40px;
    -webkit-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
}

.arrow img:hover {
    opacity: 0.8;
    -webkit-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

.hide {
  width: 50px;
  -webkit-transition: 0.4s ease-in-out;
  -o-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out;
  height: 50px;
}

.search-and-items{
    visibility: visible ;
}

.marker-clicked {
  text-align: center;
}

.gm-style:nth-child(1) {
  left:50px !important;
}

.hamburger {
    outline: none;
    position: absolute;
    right: 8px;
    height: 10%;
    -webkit-transition: 1s ease-in-out;
    -o-transition: 1s ease-in-out;
    transition: 1s ease-in-out;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    visibility: hidden;
}

.hamburger img {
    width: 50px;
    height: 50px;
}

@media screen and (max-width: 1680px) {
    .items h3 {
        font-size: 20px;
    }
    .items {
        margin-top: 10px;
    }
    .search-container {
        margin-top: 30px;
    }
    .app-name {
        font-size: 22px
    }
}

@media screen and (max-height: 780px) {
    .items h3 {
        font-size: 20px;
    }
}

@media screen and (max-width: 1500px) {
    .items h3 {
        font-size: 16px;
    }

    .search-container {
        margin-top: 30px;
    }
}

@media screen and (max-width: 1200px) {
    .items h3 {
        font-size: 13px;
    }
}

@media screen and (max-width: 1040px) {
    .sidebar {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        width: 100%;
        height: 10%;
        -webkit-transition: 0.5s ease-in-out;
        -o-transition: 0.5s ease-in-out;
        transition: 0.5s ease-in-out;
        z-index: 1000;
        background: -webkit-gradient(linear, left bottom, left top, from(#152336), to(#25435e));
        background: -webkit-linear-gradient(bottom, #152336, #25435e);
        background: -o-linear-gradient(bottom, #152336, #25435e);
        background: linear-gradient(to top, #152336, #25435e);
    }

    .items {
        margin-top: 0;
        color: #c1c1c1;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        text-align: center;
        -ms-flex-pack: center;
            justify-content: center;
    }

    .each-item-container {
        margin-top: 8px;
        margin-bottom: 8px;
    }

    .each-item-container h3 {
        margin-right: 0;
        margin-left: 0;
        font-size: 18px;
    }

    .search-and-items {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        visibility: hidden ;
    }

    #map {
        left: 0;
        top: 10%;
    }

    .marker-info {
        height: 100%;
        width: 100%;
        -ms-flex-direction: column;
            flex-direction: column;
        -webkit-transition: 0.3s ease-in-out;
        -o-transition: 0.3s ease-in-out;
        transition: 0.3s ease-in-out;
        top: 110%;
        left: 0;
        border-top-right-radius: 0;
    }

    .open-close-button {
        visibility: hidden;
    }

    .info-content img {
        max-width: 90%;
    }

    .hamburger {
        visibility: visible !important;
    }
    .app-name {
        margin-top:0;
        font-size: 20px;
    }
}


